<template>
  <div id="homepage">
    <!-- <header>
           <div class="to1" @click="click()"><i class="el-icon-search"></i></div>
           <div class="to2" @click="click2()">{{this.$route.query.name}}</div>
           <div class="to3"><i class="el-icon-user"></i></div>
    </header>-->
    <Header
      :Flagadd="false"
      :FlagHome="true"
      :FlagCity="false"
      :FlagSignin="false"
      :FlagSearch="false"
      :FlagOrder="false"
      :FlagMy="false"
      :FlagAcc="false"
    ></Header>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :finished="finished">
      <div class="content">
        <van-swipe :autoplay="3000" class="ant" indicator-color="blue">
          <van-swipe-item class="ant2" v-for="(item,index) in List" :key="index">
            <div v-for="(i,index) in item" :key="index" class="ant3">
              <img :src="`https://fuss10.elemecdn.com/${i.image_url}`" />
              <div class="ant4">{{i.title}}</div>
            </div>
          </van-swipe-item>
        </van-swipe>

        <div class="con">
          <div class="con1">附近商家</div>
          <van-list v-model="loading"  :finished="finished" finished-text="没有更多了" @load="onLoad">
            <div class="con2" v-for="(item,index) in Arr" :key="index" @click="cli(item)">
              <img :src="`http://elm.cangdu.org/img/${item.image_path}`" class="con2-1" />
              <div class="con2-2">
                <div class="conn1">
                  <span>品牌</span>
                  <span>{{item.name | ellipsis}}</span>
                  <span>保准票</span>
                </div>
                <div class="conn2">
                  <span>星星</span>
                  <span>{{item.rating}}</span>
                  <span>月售106单</span>
                  <span>蜂鸟专送</span>
                  <span>准时达</span>
                </div>
                <div class="conn3">
                  <span>￥{{item.float_minimum_order_amount}}起送/</span>
                  <span>配送费约￥{{item.float_delivery_fee}}</span>
                  <span>{{item.distance}}/{{item.order_lead_time}}</span>
                </div>
              </div>
            </div>
          </van-list>
        </div>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import axios from "axios";
import Header from "../components/Header";
import Vue from "vue";
import { List } from "vant";

Vue.use(List);
export default {
  components: {
    Header
  },
  name: "Home",
  data() {
    return {
      List: [],
      Arr: [],
      count: 0,
      isLoading: false,
      loading: false,
      finished: false,
      val: 0
    };
  },
  filters: {
    ellipsis(value) {
      if (!value) return "";
      if (value.length > 15) {
        return value.slice(0, 15) + "...";
      }
      return value;
    }
  },
  created() {
    this.axios
      .get(
        "http://elm.cangdu.org/shopping/restaurants?latitude=45.74764&longitude=126.63249&offset=0&limit=10&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]="
      )
      .then(req => {
        this.Arr = req.data;
        // console.log(this.Arr)
      });
  },
  methods: {
    //数组分割
    Division(arr) {
      //把这个数据按照8个一组 分成若干个 二个数组 [[8],[8]]
      var ArrList = [];
      for (var i = 0, j = 0; i < arr.length; i += 8, j++) {
        ArrList[j] = arr.slice(i, i + 8);
      }
      return ArrList;
    },
    click() {
      this.$router.push("/Address");
    },
    click2() {
      this.$router.push("/");
    },
    onRefresh() {
      setTimeout(() => {
        this.$toast("刷新成功");
        this.isLoading = false;
        this.count++;
      }, 500);
    },
    cli(i){
      // this.$router.push('/Nav')
      this.$router.push({path:'/Nav',query:{items:i}})
      console.log(i)

    },
    onLoad() {
        setTimeout(() => {
            if(( this.val += 10)){
                axios
                    .get(
                    `http://elm.cangdu.org/shopping/restaurants?latitude=45.74764&longitude=126.63249&offset=0&limit=${this.val}&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=`
                    )
                    .then(res => {
                    // console.log(res)
                    this.Arr = res.data
                });
                for (let i = 0; i < 10; i++) {
                    this.Arr.push(this.Arr.length + 1);
                }

                this.loading = false;
                // 加载状态结束
                if (this.Arr.length >= 40) {
                    this.finished = true;
                }
            }
        }, 1000);
    }
  },
  mounted() {
    this.axios
      .get(
        "http://elm.cangdu.org/v2/index_entry?geohash=39.78493,116.49476&group_type=1 &flags[]=F"
      )
      .then(res => {
        this.List = this.Division(res.data);
        // console.log(this.List)
      });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#homepage {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  font-size: 0.7rem;
  .content {
    width: 100%;
    flex: 1;
    overflow: auto;
    margin-top: 2rem;
    .ant {
      width: 100%;
      height: 8rem;
      background: white;
      .ant2 {
        width: 100%;
        display: flex;
        height: 8rem;
        flex-flow: column wrap;
        .ant3 {
          width: 25%;
          text-align: center;
          height: 3.5rem;
          img {
            width: 2.5rem;
          }
        }
      }
    }
    .con {
      width: 100%;
      margin-top: 0.6rem;
      background: white;
      font-size: 0.5rem;
      .con1 {
        margin-left: 0.2rem;
        margin-top: 0.5rem;
      }
      .con2 {
        display: flex;
        width: 100%;
        border-bottom: 0.01rem solid gainsboro;
        height: 3.9rem;
        margin-top: 0.7rem;
        .con2-1 {
          width: 3rem;
          height: 3rem;
          margin-left: 0.4rem;
        }
        .con2-2 {
          margin-left: 0.3rem;
          width: 70%;
          .conn1 {
            display: flex;
            position: relative;
            span:nth-of-type(1) {
              background: #ffd742;
              display: block;
              width: 1.3rem;
              height: 0.7rem;
              text-align: center;
              line-height: 0.7rem;
              border-radius: 0.1rem;
              margin-top: 0.2rem;
            }
            span:nth-of-type(2) {
              margin-left: 0.2rem;
              font-size: 0.7rem;
            }
            span:nth-of-type(3) {
              margin-left: 0.2rem;
              margin-top: 0.2rem;
              color: gray;
              position: absolute;
              right: 0;
            }
          }
          .conn2 {
            display: flex;
            margin-top: 0.5rem;
            position: relative;
            span:nth-of-type(1) {
              width: 1rem;
            }
            span:nth-of-type(2) {
              color: #d1806b;
              margin-left: 0.3rem;
            }
            span:nth-of-type(3) {
              color: #d1806b;
              margin-left: 0.3rem;
            }
            span:nth-of-type(4) {
              color: white;
              margin-left: 0.3rem;
              background: #5e93c5;
              width: 2rem;
              text-align: center;
              line-height: 0.7rem;
              display: block;
              border-radius: 0.1rem;
              font-size: 0.4rem;
              margin-left: 24%;
              position: absolute;
              right: 2rem;
            }
            span:nth-of-type(5) {
              color: #5e93c5;
              margin-left: 0.3rem;
              border: 0.01rem solid #5e93c5;
              width: 1.5rem;
              text-align: center;
              line-height: 0.7rem;
              display: block;
              border-radius: 0.1rem;
              font-size: 0.4rem;
              position: absolute;
              right: 0;
            }
          }
          .conn3 {
            margin-top: 0.5rem;
            font-size: 0.4rem;
            position: relative;

            span:nth-of-type(3) {
              color: #5e93c5;
              position: absolute;
              right: 0;
            }
          }
        }
      }
    }
  }
}
</style>



